export default {
  data: function data() {
    var validatePrice = function validatePrice(rule, value, callback) {
      if (!value) {
        return callback(new Error('Please enter price'));
      } else {
        callback();
      }
    };

    return {
      host: window.location.origin,
      filterParams: {
        name: '',
        status: '',
        createdFrom: '',
        createdTo: ''
      },
      dateArr: [],
      pagination: {
        currentPage: 1,
        pageSize: 10,
        totalCount: 0
      },
      items: []
    };
  },
  created: function created() {},
  mounted: function mounted() {
    this.getItems();
  },
  methods: {
    ResetClick: function ResetClick() {
      this.filterParams = {
        name: '',
        status: '',
        createdFrom: '',
        createdTo: ''
      };
      this.dateArr = [];
      this.getItems();
    },
    getItems: function getItems() {
      var _this = this;

      this.$apiCall('api.OfferProduct.findByCustomer', {
        status: this.filterParams.status,
        nameLike: this.filterParams.name || '',
        timeCreatedFrom: this.filterParams.createdFrom,
        timeCreatedTo: this.filterParams.createdTo,
        rowsPerPage: this.pagination.pageSize || 10,
        page: this.pagination.currentPage || 1
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this.items = r.Data.Results;
          _this.pagination.totalCount = Number(r.Data.Pagination.totalCount);
        } else {
          _this.$elementMessage(r.Message, 'error');
        }
      });
    },
    addClick: function addClick() {
      // 新增搜品
      this.publishVisible = true;
    },
    dateChange: function dateChange(arr) {
      if (arr) {
        this.filterParams.createdFrom = arr[0];
        this.filterParams.createdTo = arr[1];
      } else {
        this.filterParams.createdFrom = '';
        this.filterParams.createdTo = '';
      }
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.pagination.currentPage = val;
      this.getItems();
    },
    handleSizeChange: function handleSizeChange(val) {
      this.pagination.pageSize = val;
      this.getItems();
    }
  }
};